.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.teacher {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  border: 2px solid lightblue;
  width: 80%;
  border-radius: 10px;
  margin: 10px;
}

.teacher-info-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
}

.teacher-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  margin: 10px;
}

.teacher-name {
  font-size: 25px;
}

.main-teacher-info-container {
  display: flex;
  flex-direction: row;
}

button {
  background-color: lightblue;
  margin: 10px;
  padding: 3px;
  border-radius: 5px;
}

input {
  margin: 10px;
}